<script> 
import axios from "axios"; 
 
export default { 
  data() { 
    return { 
      Citas: [], 
    }; 
  }, 
  created() { 
    let apiURL = "http://localhost:3000/api"; 
    axios 
      .get(apiURL) 
      .then((res) => { 
        this.Citas = res.data; 
      }) 
      .catch((error) => { 
        console.log(error); 
      }); 
  }, 
  methods: { 
    deleteAppointment(id) { 
      let apiURL = `http://localhost:3000/api/delete-appointment/${id}`; 
      let indexOfArrayItem = this.Citas.findIndex((i) => i._id === id); 
 
      if (window.confirm("Do you really want to delete?")) { 
        axios 
          .delete(apiURL) 
          .then(() => { 
            this.Citas.splice(indexOfArrayItem, 1); 
          }) 
          .catch((error) => { 
            console.log(error); 
          }); 
      } 
    }, 
  }, 
}; 
</script>

